@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");

/*=====Colors====*/
$primary-color: #8c9278;
$primary-color-alt: #787e66;
$title-color: #393939;
$body-color: #fbfefd;
$container-color: #ffffff;
$text-color: #707070;
$text-color-light: #a6a6a6;

/*====Font and Typography====*/
$body-font: 'Poppins', sans-serif;
$biggest-font-size: 2.25rem;
$h1-font-size: 1.5rem;
$h2-font-size: 1.25rem;
$h3-font-size: 1rem;
$normal-font-size: 0.938rem;
$small-font-size: 0.813rem;
$smaller-font-size: 0.75rem;

/*========== Font weight ==========*/
$font-medium: 500;
$font-semi-bold: 600;

/*========== Margin ==========*/
$mb-1: 0.5rem;
$mb-2: 1rem;
$mb-3: 1.5rem;
$mb-4: 2rem;
$mb-5: 2.5rem;
$mb-6: 3rem;

/*========== z index ==========*/
$z-tooltip: 10;
$z-fixed: 100;

/*========== Border ==========*/
$border: 1px solid #474a4d;

/*========== Base ==========*/
html {
  scroll-behavior: smooth;
}

/*==========Layout==========*/
.bd-container {
  max-width: 960px;
  width: calc(100% - 2rem);
  margin-left: $mb-2;
  margin-right: $mb-2;
}

.bd-grid {
  display: grid;
  gap: 1.5rem;
}

@media screen and (min-width: 768px) {
  $biggest-font-size: 4rem;
  $h1-font-size: 2.25rem;
  $h2-font-size: 1.5rem;
  $h3-font-size: 1.25rem;
  $normal-font-size: 1rem;
  $small-font-size: 0.875rem;
  $smaller-font-size: 0.813rem;
}

.App {
  margin: 3rem 0 0 0;
  font-family: $body-font;
  font-size: $normal-font-size;
  background-color: $body-color;
  color: $text-color;
  line-height: 1.6;
}

h1,
h2,
h3,
p,
ul {
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: $text-color;
}

img {
  max-width: 100%;
  height: auto;
}

.section {
  padding: 4rem 0 3rem;
}

.section-title,
.section-subtitle {
  text-align: center;
}

.section-title {
  font-size: $h1-font-size;
  color: $title-color;
  margin: $mb-3 0 $mb-3 0;
}

.section-subtitle {
  display: block;
  color: $primary-color;
  font-weight: $font-medium;
  margin-bottom: $mb-1;
}

.button {
  display: inline-block;
  background-color: $primary-color;
  color: #fff;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  transition: 0.3s;
}

.button:hover {
  background-color: $primary-color-alt;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  padding: 0 6rem 0 2rem;
}

.navbar {
  height: 60px;
}

.navbar-nav {
  max-width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
}

.logo img {
  width: 90px;
  height: 90px;
}

.nav-item {
  width: calc(60px * 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-icon:hover {
  color: $primary-color;
  width: 20px;
}

.dropdown {
  position: absolute;
  top: 98px;
  width: 100px;
  transform: translateX(15%);
  background-color: #8c9278;
  border-radius: 8px;
  padding: 1rem;
  overflow: hidden;
}

.menu-item {
  height: 50px;
  color: #ffffff;
  display: flex;
  align-items: center;
  border-radius: 8px;
  transition: background 300ms;
}

.menu-item:hover {
  background-color: rgb(91, 95, 76);
}

.suggestion {
  padding: 40px;
}

.home-container {
  height: calc(100vh - var(--header-height));
  align-content: center;
}

.home-title {
  font-size: $biggest-font-size;
  color: $primary-color;
  margin-bottom: $mb-1;
}

.home-subtitle {
  font-size: $h1-font-size;
  color: $title-color;
  margin-bottom: $mb-4;
  line-height: 40px;
}

.home-img {
  width: 300px;
  justify-self: center;
}

/*======ABOUT=====*/

.about-data {
  text-align: center;
}

.about-description {
  margin-bottom: $mb-3;
}

.about-img {
  width: 280px;
  border-radius: 0.5rem;
  justify-self: center;
}

/*======Services=====*/

.services-container {
  row-gap: 2.5rem;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
}

.services-content {
  text-align: center;
}

.services-img {
  width: 64px;
  height: 64px;
  fill: var(--first-color);
  margin-bottom: var(--mb-2);
}

.services-title {
  font-size: var(--h3-font-size);
  color: var(--title-color);
  margin-bottom: var(--mb-1);
}

.services-description {
  padding: 0 1.5rem;
}

/*========== MENU ==========*/
.menu-container {
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
}

.menu-content {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: var(--container-color);
  border-radius: 0.5rem;
  box-shadow: 0 2px 4px rgba(3, 74, 40, 0.15);
  padding: 0.75rem;
}

.menu-img {
  width: 100px;
  align-self: center;
  margin-bottom: var(--mb-2);
}

.menu-name,
.menu-preci {
  font-weight: var(--font-semi-bold);
  color: var(--title-color);
}

.menu-name {
  font-size: var(--normal-font-size);
}

.menu-detail,
.menu-preci {
  font-size: var(--small-font-size);
}

.menu-detail {
  margin-bottom: var(--mb-1);
}

.menu-button {
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
  padding: 0.625rem 0.813rem;
  border-radius: 0.5rem 0 0.5rem 0;
}
/*========== FOOTER ==========*/
.footer__container{
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  row-gap: 2rem;
}

.footer__logo{
  font-size: var(--h3-font-size);
  color: var(--first-color);
  font-weight: var(--font-semi-bold);
}

.footer__description{
  display: block;
  font-size: var(--small-font-size);
  margin: .25rem 0 var(--mb-3);
}

.footer__social{
  font-size: 1.5rem;
  color: var(--title-color);
  margin-right: var(--mb-2);
}

.footer__title{
  font-size: var(--h2-font-size);
  color: var(--title-color);
  margin-bottom: var(--mb-2);
}

.footer__link{
  display: inline-block;
  color: var(--text-color);
  margin-bottom: var(--mb-1);
}

.footer__link:hover{
  color: var(--first-color);
}

.footer__copy{
  text-align: center;
  font-size: var(--small-font-size);
  color: var(--text-color-light);
  margin-top: 3.5rem;
}


/*====MEDIA QUERIES====*/
@media screen and (min-width: 576px) {
  .home-container,
  .about-container {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }

  .about-data,
  .about-initial,
  .app-data {
    text-align: initial;
  }

  .about-img {
    width: 380px;
    order: -1;
  }
}
@media screen and (min-width: 768px) {
  body {
    margin: 0;
  }

  .section {
    padding-top: 8rem;
  }

  .change-theme {
    position: initial;
    margin-left: $mb-2;
  }

  .home-container {
    height: 100vh;
    justify-items: center;
  }

  .services-container,
  .menu-container {
    margin-top: $mb-6;
  }

  .menu-container {
    grid-template-columns: repeat(3, 210px);
    column-gap: 4rem;
  }
  .menu-content {
    padding: 1.5rem;
  }
  .menu-img {
    width: 130px;
  }
}

@media screen and (min-width: 960px) {
  .bd-container {
    margin-left: auto;
    margin-right: auto;
  }

  .home-img {
    width: 500px;
  }

  .about-container,
  .app-container {
    column-gap: 7rem;
  }
}

/* For tall screens on mobiles y desktop*/
@media screen and (min-height: 721px) {
  .home-container {
    height: 640px;
  }
}

.suggestion h1 {
  font-size: 2em;
}

.suggestion img {
  width: 300px;
  height: 300px;
  margin-top: 30px;
}

.shop .container {
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 20px;
}

.shop .container .card {
  border: 0;
}

.shop .container .card h6 {
  font-size: 1em;
  font-weight: normal;
  font-family: 'Avenir-Book';
  margin-left: 10px;
  color: gray;
}

.shop .container .card h5 {
  font-family: 'Avenir-Book';
  font-weight: normal;
  margin-left: 10px;
}

.shop .container .card img {
  height: 180px;
}

.item-container {
  display: flex;
  flex-direction: row;
  margin-left: 130px;
}

.item-image img {
  // width: 450px;
  height: 450px;
}

.item-description {
  width: 500px;
  margin-top: 40px;
  margin-left: 40px;
}

.description-header {
  margin-bottom: 20px;
}

.description-header h1 {
  font-size: 1.6em;
  line-height: 1.1;
  letter-spacing: 2.8px;
  font-family: 'Quicksand', sans-serif;
  font-style: normal;
}

.description-header span {
  font-size: 1.2em;
  line-height: 1.1;
  letter-spacing: 2.8px;
  font-family: 'futura-pt-n7', 'futura-pt', Tahoma, Geneva, Verdana, Arial,
    sans-serif;
  font-style: normal;
  font-weight: 700;
  color: #555;
}

.description-middle {
  margin-bottom: 45px;
}

.description-middle h3 {
  font-size: 1em;
  font-weight: bold;
  letter-spacing: 1.7px;
  line-height: 1.7;
  font-family: 'futura-pt-n7', 'futura-pt', Tahoma, Geneva, Verdana, Arial,
    sans-serif;
}

.add-cart button {
  font-family: 'futura-pt-n7', 'futura-pt', Tahoma, Geneva, Verdana, Arial,
    sans-serif;
  font-style: normal;
  font-weight: 700;
  width: 50%;
  float: left;
  background-color: #018849;
  display: table;
  text-align: center;
  color: #fff;
  letter-spacing: 2px;
  height: 41px;
  text-transform: uppercase;
  border: 0;
  cursor: pointer;
  padding: 0;
  font-size: 14px;
}
.signup {
  width: 100%;
  background-color: whitesmoke;
  height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.signup-card {
  width: 30%;
  background-color: white;
  box-shadow: 0 0.625rem 1.875rem 0 rgba(138, 155, 165, 0.15);
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 40px;
}

.signup-header h1 {
  font-size: 1.5em;
  text-align: center;
  font-weight: bold;
}
.signup-header h6 {
  font-size: 0.78em;
  text-align: center;
  color: rgb(70, 67, 67);
  font-weight: normal;
}

.signup .signup-header .social {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

.signup .signup-header .social input {
  text-align: center;
  font-size: 1.2em;
  padding: 10px;
  color: black;
  font-weight: bold;
}

.or {
  &LoginDiv {
    display: flex;
    align-items: center;
    text-align: center;
    margin-top: 0.925rem;
    margin-bottom: 1.25rem;

    &::before,
    &::after {
      content: '';
      flex: 1;
      border-bottom: 0.0625rem solid gray;
      margin-right: 0.25em;
    }
  }

  &Span {
    font-size: 1.875rem;
    color: #000;
    border: 0.0625rem solid gray;
    width: 3.125rem;
    height: 3.125rem;
    border-radius: 50%;
    padding-top: 0.3125rem;

    > p {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}

footer {
  bottom: 0;
}
.footer-distributed {
  display: flex;
  justify-content: space-around;
  background-color: #000;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  font: bold 16px sans-serif;
  padding: 55px 50px;
  margin-top: 80px;
}

.footer-address {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}

@media (max-width: 880px) {
  .footer-distributed {
    font: bold 14px sans-serif;
  }

  .footer-distributed .footer-left,
  .footer-distributed .footer-center,
  .footer-distributed .footer-right {
    display: block;
    width: 100%;
    margin-bottom: 40px;
    text-align: center;
  }

  .footer-distributed .footer-center i {
    margin-left: 0;
  }
  .main {
    line-height: normal;
    font-size: auto;
  }
}

.footer-right {
  width: 30%;
}

.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right h5 {
  text-decoration: none;
  font-family: 'Avenir-Book';
  font-size: 0.9em;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.footer-right .footer-about p {
  color: white;
  font-size: 0.7em;
  line-height: 30px;
  text-align: justify;
  margin-top: 20px;
  font-weight: normal;
}

.footer-center .footer-links {
  margin-top: 20px;
  font-weight: normal;
}

.footer-center .footer-links a {
  color: rgb(250, 246, 246);
  font-size: 0.9em;
  line-height: 30px;
  text-align: justify;
  text-decoration: none;
}

.footer-center .footer-links a:hover {
  text-decoration: none;
  color: #8c9278;
}

.footer-left .footer-icons {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 20px;
}

.footer-left .footer-icons a {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  text-align: center;
}

.footer-left .footer-icons .fa {
  color: #fff;
  font-size: 1.3em;
  line-height: 42px;
}

.about {
  // background: url(./assets/images/bold-yarn.jpg) no-repeat left;
  background-size: 55%;
  background-color: #fdfdfd;
  overflow: hidden;
  padding: 80px 0;
}
.inner-section {
  width: 55%;
  float: right;
  background-color: #fdfdfd;
  padding: 60px;
  box-shadow: 10px 10px 8px rgba(0, 0, 0, 0.3);
}
.inner-section h1 {
  margin-bottom: 20px;
  font-size: 1.2em;
  font-weight: 900;
}
.text {
  font-size: 13px;
  color: #545454;
  line-height: 30px;
  text-align: justify;
  margin-bottom: 40px;
}

@media screen and (max-width: 1200px) {
  .inner-section {
    padding: 80px;
  }
}
@media screen and (max-width: 1000px) {
  .about {
    background-size: 100%;
    padding: 100px 40px;
  }
  .inner-section {
    width: 100%;
  }
}

@media screen and (max-width: 600px) {
  .about {
    padding: 0;
  }
  .inner-section {
    padding: 60px;
  }
}
